<template>
    <main id="art-cycle" class="p-m-2">
        <div class="p-grid">
            <div class="p-col-12 p-md-6">
                <patient-search title="Female" type="female" />
            </div>
            <div class="p-col-12 p-md-6">
                <patient-search title="Male" type="male" />
            </div>
        </div>

        <div class="p-grid">
            <div class="p-col-12">
                <cycle-form />
            </div>
        </div>

        <div style="height: calc(100vh - 575px)">
            <cycle-table />
        </div>

        <div class="p-grid p-mt-1">
            <div class="p-col-12 p-md-6">
                <lab-test type="female" />
            </div>
            <div class="p-col-12 p-md-6">
                <lab-test type="male" />
            </div>
        </div>

        <p-button type="button" label="Save" class="p-mr-2"></p-button>
    </main>
</template>

<script>
import { onMounted, provide, reactive, ref } from "vue";
import { emitter } from "@/event-bus";

import PatientSearch from "@/components/art-cycle/PatientSearch.vue";
import CycleTable from "@/components/art-cycle/CycleTable.vue";
import CycleForm from "@/components/art-cycle/CycleForm.vue";
import LabTest from "@/components/art-cycle/LabTest.vue";

export default {
    components: {
        PatientSearch,
        CycleTable,
        CycleForm,
        LabTest,
    },
    setup() {
        const dropdownOpts = ref([
            { label: "Opt 1", value: "opt-1" },
            { label: "Opt 2", value: "opt-2" },
            { label: "Opt 3", value: "opt-3" },
        ]);

        const cycleTable = ref([
            {
                id: "200001",
                cycleNo: "1",
                cycleType: "IVF",
                cycleNoType: "3",
                spermSource: null,
                eggSource: null,
                eggDonor: null,
                other: null,
                cycleDate: "01-02-20201",
                incomplete: false,
                complete: false,
                funded: false,
                cancel: true,
            },
        ]);

        provide("cycles", cycleTable);

        emitter.on("cycle-table:new-cycle", (data) => {
            cycleTable.value.push(data)
        })

        return {
            cycleTable,
            dropdownOpts,
        };
    },
};
</script>

