</template>

<script>
import { computed, reactive, ref } from "vue";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import { PatientType } from "@/config.js";

import Service from "@/service/art-cycle.js";

export default {
    props: {
        type: String,
    },
    setup(props) {
        const title = computed(() => {
            if (props.type === PatientType.FEMALE) return "Female";
            if (props.type === PatientType.MALE) return "Male";
        });

        const sex = computed(() => {
            if (props.type === PatientType.FEMALE) return "f";
            if (props.type === PatientType.MALE) return "m";
        });

        const demographic = ref();
        const fillteredDemographic = ref();
        const searchDemographic = (event) => {
            Service.searchDemographic({
                term: event.query.trim(),
                sex: sex.value,
            })
                .then((resp) => {
                    fillteredDemographic.value = resp.data;
                })
                .catch((err) => {
                    emitter.emit(EVENT_STRINGS.LOGOUT);
                });
        };

        const dropdownOpts = ref([
            { label: "Opt 1", value: "opt-1" },
            { label: "Opt 2", value: "opt-2" },
            { label: "Opt 3", value: "opt-3" },
        ]);

        return {
            title,
            demographic,
            fillteredDemographic,
            searchDemographic,
            dropdownOpts,
        };
    },
};
</script>