<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid p-input-filled">
                <div class="p-field p-col-12 p-md-2">
                    <label for="cycle-type">Cycle Type</label>
                    <p-dropdown
                        v-model="cycleType"
                        id="cycle-type"
                        :options="cycleTypeOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div class="p-field p-col-12 p-md-2">
                    <label for="cycle-type">Fundend</label>
                    <p-dropdown
                        v-model="funded"
                        id="cycle-type"
                        :options="dropdownOpts"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div class="p-field p-col-12 p-md-2">
                    <label for="cycle-type">Cycle Cacelled</label>
                    <p-dropdown
                        v-model="cancelled"
                        id="cycle-type"
                        :options="dropdownOpts"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div class="p-field p-col-12 p-md-2">
                    <label for="cycle-type">Sperm Soruce</label>
                    <p-dropdown
                        v-model="spermSource"
                        id="cycle-type"
                        :filter="true"
                        :options="spermSourceOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div class="p-field p-col-12 p-md-2">
                    <label for="cycle-type">Egg Source</label>
                    <p-dropdown
                        v-model="eggSource"
                        id="cycle-type"
                        :filter="true"
                        :options="eggSourceOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div class="p-field p-col-12 p-md-1">
                    <label for="egg-donor">Egg Donor</label>
                    <br />
                    <p-toggle-button
                        v-model="eggDonor"
                        id="egg-donor"
                        onIcon="pi pi-check"
                        offIcon="pi pi-times"
                    />
                </div>
                <div class="p-field p-col-12 p-md-1">
                    <label for="compete">Complete</label>
                    <br />
                    <p-toggle-button
                        v-model="complete"
                        id="complete"
                        onIcon="pi pi-check"
                        offIcon="pi pi-times"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { onMounted, ref } from "vue";
import { emitter } from "@/event-bus";
import Service from "@/service/art-cycle.js";

export default {
    setup() {
        const dropdownOpts = ref([
            { label: "Opt 1", value: "opt-1" },
            { label: "Opt 2", value: "opt-2" },
            { label: "Opt 3", value: "opt-3" },
        ]);

        const cycleTypeOptions = ref();
        const spermSourceOptions = ref();
        const eggSourceOptions = ref();

        const cycleType = ref();
        const funded = ref();
        const cancelled = ref();
        const spermSource = ref();
        const eggSource = ref();
        const eggDonor = ref();
        const complete = ref();

        onMounted(() => {
            Service.getFormFieldOptions().then((resp) => {
                cycleTypeOptions.value = resp.data.cycleTypes;
                spermSourceOptions.value = resp.data.spermSources;
                eggSourceOptions.value = resp.data.eggSource;
            });
        });

        const fillFormValues = (data) => {
            cycleType.value = data.cycleType;
            funded.value = data.funded;
            cancelled.value = data.cancelled;
            spermSource.value = data.value;
            eggSource.value = data.eggSource;
            eggDonor.value = data.eggDonor;
            complete.value = data.complete;
        };

        emitter.on("cycle-table:cycles-seleted", fillFormValues);

        return {
            dropdownOpts,
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            cycleType,
            funded,
            cancelled,
            spermSource,
            eggSource,
            eggDonor,
            complete,
        };
    },
};
</script>