<template>
    <p-data-table
        :value="cycles"
        dataKey="id"
        v-model:selection="selectedCycle"
        selectionMode="single"
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :scrollable="true"
        scrollHeight="flex"
        sortField="cycleNo"
        :sortOrder="-1"
    >
        <p-column selectionMode="single" style="max-width: 3rem" />
        <p-column field="id" header="#" style="max-width: 120px" />
        <p-column field="cycleNo" header="Cycle No" style="max-width: 120px" />
        <p-column
            field="cycleType"
            header="Cycle Type"
            style="max-width: 120px"
        />
        <p-column
            field="cycleNoType"
            header="Cycle No Type"
            style="max-width: 120px"
        />
        <p-column field="funded" header="Funded" style="max-width: 7rem">
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.funded"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column field="cancel" header="Cancel" style="max-width: 7rem">
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.cancel"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="cycleDate"
            header="Cycle Date"
            style="max-width: 120px"
        />
        <p-column
            field="incomplete"
            header="Incomplete"
            style="max-width: 7rem"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.incomplete"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column field="complete" header="Complete" style="max-width: 7rem">
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.complete"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="spermSource"
            header="Sperm Source"
            style="max-width: 120px"
        />
        <p-column
            field="eggSource"
            header="Egg Source"
            style="max-width: 120px"
        />
        <p-column
            field="eggDonor"
            header="Egg Donor"
            style="max-width: 120px"
        />
        <p-column header="" />

        <template #footer>
            <p-button label="Save Cycle" class="p-mr-1" @click="save" />
            <p-button label="New Cycle" class="p-mr-1" @click="newCycle" />
            <p-button label="Merge Cycle" class="p-mr-1" />
            <p-button label="Delete Cycle" class="p-mr-1" />
            <p-button label="PDF Report" class="p-mr-1" />
            <p-button label="Other Blood Test" class="p-mr-1" />
            <p-button label="Import HL7 Message" class="p-mr-1" />
            <p-button label="Cryostorage Report" class="p-mr-1" />
            <p-button label="Copules Report" class="p-mr-1" />
        </template>
    </p-data-table>
</template>

<script>
import { inject, onMounted, ref, watch } from "vue";
import { emitter } from "@/event-bus";

export default {
    setup() {
        const cycles = inject("cycles");
        const selectedCycle = ref();

        watch(selectedCycle, (currentValue, oldValue) => {
            emitter.emit("cycle-table:cycles-seleted", currentValue);
        });

        const save = () => {
            console.log(selectedCycle.value);
        };

        const newCycle = () => {
            let newCycle = {
                id: "100001",
                cycleNo: cycles.value.length + 1,
                cycleType: "IVF",
                cycleNoType: "3",
                spermSource: null,
                eggSource: null,
                eggDonor: null,
                other: null,
                incomplete: false,
                complete: false,
                funded: false,
                cancel: false,
            };

            emitter.emit("cycle-table:new-cycle", newCycle);
            selectedCycle.value = newCycle;
        };

        onMounted(() => {
            selectedCycle.value = cycles.value[0];
        });

        return {
            cycles,
            selectedCycle,
            save,
            newCycle,
        };
    },
};
</script>

<style scoped>
.p-datatable {
    background-color: white;
}
</style>